@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

* { 
    font-family: 'Spoqa Han Sans Neo', 'sans-serif'; 
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}
::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}

body {
    background: black;
    color: rgb(226, 226, 226);
    margin: 0;
    padding: 0;

    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}

.box {
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
    height: 5%;
    position: fixed;
    z-index: 3;
    background: black;
    /* background: linear-gradient(to bottom, #000 , transparent); */
    opacity: .9;
}
/* .box_mask {
    width: 100%;
    height: 100%;
    mask-image: linear-gradient(#000, transparent);
} */

.container {
    position: fixed;
    left: 2%;
    right: 2%;
    top: 5%;
    bottom: 5%;
    overflow: hidden;
    z-index: 0;
}
.mask {
    width: 100%;
    height: 100%;
    mask-image: linear-gradient(#000, transparent)
}
#bg_img {
    width: 105%;
    height: 105%;
    object-fit: cover;
    opacity: .5;
}

.menu_box {
    position: fixed;
    left: 4.5%;
    right: 80%;
    top: 5%;
    bottom: 30%;
    overflow: hidden;
    z-index: 2;
}
header {
    font-size: 1.4em;
    user-select: none;
    /* padding-left: 1%; */
}
ol {
    list-style: none;
    padding-left: 2%;
}
ol li {
    margin-bottom: 4px;
}
a {
    text-decoration: none;
}
a:link, a:visited {
    color: rgb(226, 226, 226);
}
a:hover, a:active {
    color: rgb(189, 189, 189);
    opacity: .5;
}

.contents {
    position: relative;
    z-index: 1;
}
main {
    padding-top: 4%;
    padding-right: 4%;
    text-align: right;
}
#index {
    position: fixed;
    right: 4.5%;
    bottom: 7%;
}
button {
    font-size: 0.9em;
    background: none;
    color: rgb(226, 226, 226);
    cursor: pointer;
    margin: 0;
    padding: 0;
    border: none;
}
#button_list li{
    margin-bottom: 5px;
}
